import "./Programmer.css";
import { motion } from "framer-motion";
import LanguageContext from "../context/LanguageContext";
import { useContext } from "react";

export default function Programmer() {
  const { texts } = useContext(LanguageContext);

  return (
    <motion.div
      initial={{ y: "200vh" }}
      animate={{ y: "0", transition: { duration: 1, ease: "circOut" } }}
    >
      <section className="black-prog">
        <img
          className="fondo-prog"
          src="images/music/vector_programming.png"
          alt="img_programmer"
        />
        <div className="header_black-prog">
          <h2 className="programmer-tit"> {texts.prog.tit}</h2>
        </div>

        <div className="soft-prog">
          <h3>{texts.prog.proyectos.proyecto}</h3>

          <div className=" info-proyecto">
            <div className="link-proyecto">
              <a
                href="https://criptoapp-svelte.vercel.app/"
                target="_blank"
                rel="noopener noreferrer"
              >
                {texts.prog.proyectos.ver}
              </a>
              <br />
              <a
                href="https://github.com/ajcappo/criptoapp_svelte.git"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img src="images/git.svg" alt="git" />
              </a>
            </div>

            <p className="p-proyecto">{texts.prog.proyectos.proy3}</p>
          </div>

          <div className=" info-proyecto">
            <div className="link-proyecto">
              <a
                href="https://ajcappo.github.io/Pagina-web-UTN/"
                target="_blank"
                rel="noopener noreferrer"
              >
                {texts.prog.proyectos.ver}
              </a>
              <br />
              <a
                href="https://github.com/ajcappo/Pagina-web-UTN"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img src="images/git.svg" alt="git" />
              </a>
            </div>

            <p className="p-proyecto">{texts.prog.proyectos.proy1}</p>
          </div>

          <div className=" info-proyecto">
            <div className="link-proyecto">
              <a
                href="https://ajcappo.github.io/bg-color-generator/"
                target="_blank"
                rel="noopener noreferrer"
              >
                {texts.prog.proyectos.ver}
              </a>
              <br />
              <a
                href="https://github.com/ajcappo/bg-color-generator"
                target="_blank"
                rel="noopener noreferrer"
              >
                <img src="images/git.svg" alt="git" />
              </a>
            </div>

            <p className="p-proyecto">{texts.prog.proyectos.proy2}</p>
          </div>
        </div>

        <div className="certificaciones">
          <h3>{texts.prog.certificados.title}</h3>
          <a
            className="certificado"
            href="https://drive.google.com/file/d/1UV-VbnFdAlAKzvn4lUfPjoReYM4zZiRS/view?usp=sharing"
            target="_blank"
            rel="noopener noreferrer"
          >
            {texts.prog.certificados.cert1}
          </a>
          <br />
          <a
            className="certificado"
            href="https://drive.google.com/file/d/1neuOpUuOmvkoofywQ9h1T2YGf3IKkmBu/view?usp=sharing"
            target="_blank"
            rel="noopener noreferrer"
          >
            {texts.prog.certificados.cert2}
          </a>
          <br />
          <a
            className="certificado"
            href="https://drive.google.com/file/d/1Z7i0cC72zRvmivBmdemgyjPdzdSlrm4_/view?usp=sharing"
            target="_blank"
            rel="noopener noreferrer"
          >
            {texts.prog.certificados.cert3}
          </a>
          <br />
          <a
            className="certificadoFinal"
            href="https://drive.google.com/file/d/1wVh3f9peu0DoJKsLs6BmCdMUpngXH76N/view?usp=sharing"
            target="_blank"
            rel="noopener noreferrer"
          >
            {texts.prog.certificados.cert4}
          </a>
        </div>

        <div className="soft-prog">
          <h3>{texts.prog.lenguajes}</h3>
          <div className="icons-prog">
            <img className="icon-prog" src="../icons/html-5.png" alt="html5" />
            <img className="icon-prog" src="../icons/css-3.png" alt="css3" />
            <img className="icon-prog" src="../icons/js6.png" alt="js" />
          </div>
          <br />

          <h3>{texts.prog.frameworks}</h3>
          <div className="icons-prog">
            <img
              className="icon-prog"
              src="../icons/bootstrap.png"
              alt="bootstrap"
            />
            <img className="icon-prog" src="../icons/react.png" alt="react" />
            <img className="icon-prog" src="../icons/vue.png" alt="vue" />
          </div>
          <br />
          <h3>{texts.prog.db}</h3>
          <div className="icons-prog">
            <img className="icon-prog" src="../icons/php.png" alt="php" />
            <img className="icon-prog" src="../icons/mySQL.png" alt="sql" />
            <img
              className="icon-prog"
              src="../icons/mongodb.png"
              alt="mongodb"
            />
          </div>
          <h3>{texts.prog.soft}</h3>
          <div className="icons-prog">
            <img className="icon-prog" src="../icons/vsc.png" alt="vsc" />
            <img className="icon-prog" src="../icons/figma.png" alt="figma" />
            <img className="icon-prog" src="../icons/ai.png" alt="ai" />
            <img className="icon-prog" src="../icons/ps.png" alt="ps" />
          </div>
        </div>
      </section>
    </motion.div>
  );
}
