import "./Music.css"
import { motion } from "framer-motion";
import { useContext } from "react";
import LanguageContext from "../context/LanguageContext";

export default function Music() {
    const {texts} = useContext(LanguageContext)

    return (
        <motion.div
                initial={{y: "200vh"}}
        animate={{y: "0", transition:{ duration:1, ease:"circOut"}}}

>

        <section className="black-mus">
        <img className="fondo-mus" src="images/music/vector_music.png" alt="img_music"/>

            <div className="header_black-mus">

                <h2 className="musico-tit">{texts.music.tit}</h2>
                
            
            </div>
    
            <div className="info-mus">
                <h3>
                    {texts.music.titulo}
                </h3>
                <p> {texts.music.tituloDesc}</p>
            </div>
            <div className="info-mus">
                <h3>
                {texts.music.inst}
                </h3>
                <p> {texts.music.instDesc}</p>
            </div>
            <div className="info-mus">
                <h3>
                {texts.music.form}
                </h3>
                <div className="grupos-mus">
    
                    <p> {texts.music.formDesc.f1}</p>
                    <p> {texts.music.formDesc.f2}</p>
                    <p> {texts.music.formDesc.f3}</p>
                    <p> {texts.music.formDesc.f4}</p>
                    <h2 className="h2_black-mus">
                    {texts.music.formDesc.f5} 
                       
                    </h2>
                    <div className="video-mus"></div>
                </div>
    
    
            </div>
            <div className="info_video-mus">
                
                    <iframe className="vid-mus" src="https://www.youtube.com/embed/CigglHz1QvI" title="YouTube video player" frameBorder="0"
                      allow="accelerometer; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                      allowFullScreen></iframe>
                      <iframe className="vid-mus" src="https://www.youtube.com/embed/VvguOxYcU_A" title="YouTube video player" frameBorder="0"
                      allow="accelerometer; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                      allowFullScreen></iframe>
                      <iframe className="vid-mus" src="https://www.youtube.com/embed/IiZR1QrbrCc" title="YouTube video player" frameBorder="0" allow="accelerometer; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                      <iframe className="vid-mus" src="https://www.youtube.com/embed/khqOdiNiOS0" title="YouTube video player" frameBorder="0"
                      allow="accelerometer; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                      allowFullScreen></iframe>
                      <iframe className="vid-mus" src="https://www.youtube.com/embed/4E7sUQF4ExM" title="YouTube video player" frameBorder="0" allow="accelerometer; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                      <iframe className="vid-mus" src="https://www.youtube.com/embed/afG-KEa7pcg" title="YouTube video player" frameBorder="0" allow="accelerometer; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>

            </div>

        </section>




        </motion.div>
    )
}
