import "./Header.css";
import { BrowserRouter, Link, Switch, Route } from "react-router-dom"


import Music from "./Music"
import Geology from "./Geology"
import Programmer from "./Programmer"
import Home from "./Home"
import Formulario from "./Formulario";
import LanguageContext from "../context/LanguageContext";
import { useContext } from "react";




function Header() {
  const {texts} = useContext(LanguageContext)



  return (
<>
      <BrowserRouter className="logo">
        <header className="header-menu">
          <Link to="/" className="inicio-menu">
            <h1 className="h1-menu">ADRIÁN <br />BAZZANA</h1>
          </Link>
          <div className="botonotes">

            <Link to="/programming" className="programador-menu">{texts.header.prog}</Link>
            <Link to="/music" className="musico-menu" >{texts.header.music}</Link>
            <Link to="/geology" className="geologo-menu">{texts.header.geo}</Link>
            <Link to="/contact" className="contact-menu">{texts.header.contact}</Link>


            
          </div>


          <nav className="navbar_580">
            <a href="/programming" className="programador-menu2">PROGRAMADOR</a>
            <a href="/music" className="musico-menu2" >MÚSICO</a>
            <a href="/geology" className="geologo-menu2">GEÓLOGO</a>
            <a href="/contact" className="contact-menu2">CONTACTO</a>



          </nav>
        </header>

          <Switch >
            <Route 
              exact path="/">
              <Home/>
            </Route>
            <Route exact path="/music">
              <Music/>
            </Route>
            <Route exact path="/geology">
              <Geology/>
            </Route>
            <Route exact path="/programming">
              <Programmer/>
            </Route>
            <Route exact path="/contact">
              <Formulario/>
            </Route>

          </Switch>


      </BrowserRouter>


    </>
  )
}

export default Header