import "./Geology.css"
import { motion } from "framer-motion";
import LanguageContext from "../context/LanguageContext";
import { useContext } from "react";

export default function Geology() {
    const {texts} = useContext(LanguageContext)

    return (
        <motion.div
                initial={{y: "200vh"}}
        animate={{y: "0", transition:{ duration:1, ease:"circOut"}}}
>
            <section className="black-geo">
                <img className="fondo-geo" src="images/music/vector_geology.png" alt="img_geology" />
                <div className="header_black-geo">

                    <h2 className="geologo-tit">{texts.geo.tit}</h2>

                </div>
                <div className="info-geo">
                    <h3>
                    {texts.geo.titulo}
                    </h3>
                    <p> {texts.geo.tituloDesc}</p>
                </div>
                <div className="info-geo">
                    <h3>
                    {texts.geo.inst}
                    </h3>
                    <p> {texts.geo.instDesc}</p>
                </div>

                <div className="info-geo">
                    <h3>
                    {texts.geo.exp}
                    </h3>
                    <div className="grupos-geo">

                        <p> {texts.geo.expDesc.e1}</p>
                        <p> {texts.geo.expDesc.e2}</p>
                        <p> {texts.geo.expDesc.e3}</p>
                        <p> {texts.geo.expDesc.e4}</p>



                    </div>


                </div>
            </section>



        </motion.div>    )
}
