import "./Home.css"
import { motion } from "framer-motion";
import { useContext } from "react";
import LanguageContext from "../context/LanguageContext";



export default function Home() {

const {texts} = useContext(LanguageContext)


    return (
        <motion.div
            initial={{ y: "-100vh" }}
            animate={{ y: "0", transition: { duration: 1, ease: "backInOut" } }}
        >


            <section className="black-home">
                <img className="fondo_home" src="images/img_verde.png" alt="img_fondo" />
                    <h2 className="h2_black-home">
                        {texts.home.mssg}
                    </h2>
                <div className="perfil">
                <div className="caja-foto">

<img className="foto-perfil" src="images/fotoPerfil.png" alt="foto-perfil" />
</div>

                <p className="p_black-home">
                {texts.home.mssg2}
                </p>

                </div>

            </section>





        </motion.div>
    )
}



